export default function procvalue(course)
{
    let header = ["Khoa ch qun HP", "M HP",	"Tn HP", "S TC", "M LHP", "Tnh trng LHP", "Loi HP", "Lp sinh vin",
                                 "SS Ti thiu"	,"SS Ti a"	,"SS  ng k"	,"SS cn trng"	,"Phng hc"	," lch sc cha"	,"Ngy B"	,"Ngy KT"	,"Thi kha biu"	,"M GVGD"	,"Tn GVGD"]
    let data = []
    let dataFind = {}
    let check = {}
    course.map((val1, index1) => {
        val1.row.map((val2, index2) => {
            if(!val2[1] ||val2[0] == header[0] || val2[1] == header[0])
            {
                
            }
            else{
                let row = {}
                header.map((v, e) => {
                    if(val2[0])
                    {
                        row[v] = val2[e]
                    }
                    else{
                        row[v] = val2[e + 1]
                    }
                })
                data.push(row)
                dataFind[row["M LHP"]] = row
                check[row["M LHP"]] = false
            }

        })
    })
    return {data, dataFind, check}
}